import React, {useState} from 'react'
import { Link } from 'react-router-dom'
import { Card, Input, Form, FormGroup, Table, Row, Col, Button } from 'reactstrap';
import DatePicker from 'react-date-picker'


import Dropzone from "react-dropzone";

import placeholder from '../../../../assets/images/admin/banner-placeholder.png'
import close from '../../../../assets/images/admin/close-image.png'


function AddBannerContent(props) {
    const [date, onChange] = useState(new Date())
    const [selectedFiles, setSelectedFiles] = useState([])

    function handleAcceptedFiles(files){
        files.map(file =>
            Object.assign(file, {
              preview: URL.createObjectURL(file),
              formattedSize: formatBytes(file.size)
            })
          );
      
          setSelectedFiles(files); 
    }

    /**
     * Formats the size
     */
    function formatBytes(bytes, decimals = 2){
        if (bytes === 0) return "0 Bytes";
        const k = 1024;
        const dm = decimals < 0 ? 0 : decimals;
        const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

        const i = Math.floor(Math.log(bytes) / Math.log(k));
        return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
    };

    return (
        <>
            <div className="main-title">
                <h1>Add Banner</h1>
            </div>
            <Form>
                <FormGroup className="banner">
                    <div className="dropzone-box" style={selectedFiles[0] ? {backgroundImage: `url(${ selectedFiles[0] && selectedFiles[0].preview })`} : {}}>
                        {selectedFiles[0] ? null  :
                            <Dropzone
                                onDrop={acceptedFiles =>
                                    handleAcceptedFiles(acceptedFiles)
                                }
                            >
                                {({ getRootProps, getInputProps }) => (
                                <div className="dropzone">
                                    <div
                                    className="dz-message needsclick"
                                    {...getRootProps()}
                                    >
                                        <input {...getInputProps()} accept=".png, .jpg" multiple={false}/>
                                        <img src={placeholder} alt="placeholder"/>
                                        <div className="d-md-flex align-items-md-center justify-content-md-center mt-4">
                                            <p className="small">Drag and Drop Banner Image here or </p>
                                            <Button type="button" className="common-btn color-blue">BROWSE</Button>
                                        </div>
                                    </div>
                                </div>
                                )}
                            </Dropzone>
                        }
                       {selectedFiles[0] ? <a href="#" className="remove"><img src={close} alt="remove"/></a> : null}
                    </div>
                </FormGroup>
                <FormGroup>
                    <label>Title</label>
                    <Input type="text"></Input>
                </FormGroup>                
                <Row>
                    <Col md="6">
                        <FormGroup className="date-field">
                            <label>From</label>
                            <DatePicker
                                onChange={onChange}
                                value={date}
                                className="form-control"
                            />
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup className="date-field">
                            <label>To</label>
                            <DatePicker
                                onChange={onChange}
                                value={date}
                                className="form-control"
                            />
                        </FormGroup>
                    </Col>
                </Row>
                <div className="text-center">
                    <Button className="common-btn color-blue">ADD BANNER</Button>
                </div>
            </Form>
        </>
    )
}

export default AddBannerContent
