import React, { Component } from "react";
import { Row, Col, Card, CardBody, Alert } from "reactstrap";

// Redux
import { connect } from "react-redux";
import { withRouter, Link } from "react-router-dom";

// availity-reactstrap-validation
import { AvForm, AvField } from "availity-reactstrap-validation";

import Loader from "../../../components/Loader";
// actions
import { loginUser } from "../../../store/actions";

// import images
import logo from "../../../assets/images/admin/logo.svg"

{/* Match Url */ }
// const { url } = props.match
// const showAccountPages = [`${process.env.PUBLIC_URL}/login`].indexOf(url) > -1;
//  if(showAccountPages){
//      document.body.classList.add('bg-secondary','add-user');
//  }

class Login extends Component {  
  constructor(props) {
    super(props);
    this.state = {};

    // handleValidSubmit
    this.handleValidSubmit = this.handleValidSubmit.bind(this);
  }


  // handleValidSubmit
  handleValidSubmit(event, values) {
    this.props.loginUser(values, this.props.history);
  }

  render() {
    return (
      <React.Fragment>
        <div className="account-pages my-5 pt-5">
          <div className="container">
            <Row className="justify-content-center">
              <Col md={8} lg={6} xl={5}>
                <div className="position-relative">
                  {this.props.loading ? <Loader /> : null}

                  <Card className="overflow-hidden">
                    <div className="login-header">
                        <h2 className="text-white">
                          Welcome Back !
                        </h2>
                        <p className="text-white-50">
                          Sign in to 999Tee Admin
                        </p>
                        <Link to={`${process.env.PUBLIC_URL}/login`} className="logo logo-admin">
                          <img src={logo} alt="logo" />
                        </Link>
                    </div>

                    <CardBody className="p-4">
                      <div className="p-3">
                        <AvForm
                          className="form-horizontal mt-4"
                          onValidSubmit={this.handleValidSubmit}
                        >
                          {this.props.error ? (
                            <Alert color="danger">{this.props.error}</Alert>
                          ) : null}

                          <div className="form-group">
                            <AvField
                              name="email"
                              label="Email"
                              className="form-control"
                              placeholder="Enter email"
                              type="email"
                              required
                            />
                          </div>
                          <div className="form-group">
                            <AvField
                              name="password"
                              label="Password"
                              type="password"
                              required
                              placeholder="Enter Password"
                            />
                          </div>

                          <Row className="form-group">
                            <Col sm={12} className="text-center">
                              <button
                                className="common-btn color-blue"
                                type="submit"
                              >
                                Log In
                              </button>
                            </Col>
                          </Row>
                          <Row className="form-group mt-2 mb-0">
                            <div className="col-12 forgot-link">
                              <Link to={`${process.env.PUBLIC_URL}/forgot`}>
                                Forgot your password?
                              </Link>
                            </div>
                          </Row>
                        </AvForm>
                      </div>
                    </CardBody>
                  </Card>
                </div>
                <p className="small text-center">&copy; 2020 999Tee</p>
              </Col>
            </Row>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStatetoProps = state => {
  const { error, loading } = state.Login;
  return { error, loading };
};

export default withRouter(connect(mapStatetoProps, { loginUser })(Login));
