import React, {useState,useEffect} from 'react'
import { Col, Row, Form, Input, FormGroup, Button,Card } from 'reactstrap';
import { Link } from 'react-router-dom'
import Dropzone from "react-dropzone";


// Form Editor
import { Editor } from "react-draft-wysiwyg";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";

import file_icon from '../../../../assets/images/admin/file-icon.svg'



function AddPackageContent(props) {
    const [selectedFiles, setSelectedFiles] = useState([])

    function handleAcceptedFiles(files){
        files.map(file =>
            Object.assign(file, {
              preview: URL.createObjectURL(file),
              formattedSize: formatBytes(file.size)
            })
          );
      
          setSelectedFiles(files); 
    }

    /**
     * Formats the size
     */
    function formatBytes(bytes, decimals = 2){
        if (bytes === 0) return "0 Bytes";
        const k = 1024;
        const dm = decimals < 0 ? 0 : decimals;
        const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

        const i = Math.floor(Math.log(bytes) / Math.log(k));
        return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
    };

    useEffect(() => {
        document.body.classList.add('bg-secondary');
        return () => {
          // componentWillUnmount events
          document.body.classList.remove('bg-secondary');
        }
      }, [])
    return (
        <>
            <div className="main-title">
                <h1>Add Package</h1>
            </div>
            <Form className="add-package">
                <Row>
                    <Col md="6">
                        <FormGroup>
                            <label>Title</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Amount</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="12">
                        <FormGroup>
                            <label>Description</label>
                            <Editor/>
                        </FormGroup>
                    </Col>
                    <Col md="12">
                        <FormGroup>
                            <Dropzone
                                onDrop={acceptedFiles =>
                                    handleAcceptedFiles(acceptedFiles)
                                }
                            >
                                {({ getRootProps, getInputProps }) => (
                                <div className="dropzone">
                                    <div
                                    className="dz-message needsclick"
                                    {...getRootProps()}
                                    >
                                        <input {...getInputProps()} />
                                        <img src={file_icon} alt="icon"/>
                                        <p className="small">Upload Package Detail Document</p>
                                    </div>
                                </div>
                                )}
                            </Dropzone>
                            <div
                                className="dropzone-previews mt-3"
                                id="file-previews"
                            >
                                {selectedFiles.map((f, i) => {
                                return (
                                    <Card
                                    className="mt-1 mb-0 shadow-none border dz-processing dz-image-preview dz-success dz-complete"
                                    key={i + "-file"}
                                    >
                                        <div className="p-2">
                                            <Row className="align-items-center">
                                                <Col className="col-auto">
                                                    <img
                                                    data-dz-thumbnail=""
                                                    height="80"
                                                    className="avatar-sm rounded bg-light"
                                                    alt={f.name}
                                                    src={f.preview}
                                                    />
                                                </Col>
                                                <Col>
                                                    <Link
                                                        to="#"
                                                        className="text-muted font-weight-bold"
                                                    >
                                                    {f.name}
                                                    </Link>
                                                    <p className="mb-0">
                                                        <strong>{f.formattedSize}</strong>
                                                    </p>
                                                </Col>
                                            </Row>
                                        </div>
                                    </Card>
                                    );
                                })}
                            </div>
                        </FormGroup>
                    </Col>
                </Row>
                <div className="text-center">
                    <Button type="submit" className="common-btn color-blue">SUBMIT</Button>
                </div>
            </Form>
        </>
    )
}

export default AddPackageContent
