import React, { useState, useEffect, Component } from "react";
import { withRouter, Link } from "react-router-dom";
import { Collapse} from 'reactstrap';

import SimpleBar from "simplebar-react";
import classnames from "classnames"

/** icon images */
import dashboard from '../../assets/images/admin/dashboard-icon.svg'
import roles from '../../assets/images/admin/manage-roles-icon.svg'
import users from '../../assets/images/admin/manage-users-icon.svg'
import b2b_users from '../../assets/images/admin/manage-b2b-users-icon.svg'
import artists from '../../assets/images/admin/manage-artists-icon.svg'
import products from '../../assets/images/admin/manage-products-icon.svg'
import orders from '../../assets/images/admin/manage-orders-icon.svg'
import support from '../../assets/images/admin/manage-support-icon.svg'
import reports from '../../assets/images/admin/reports-icon.svg'
import marketing from '../../assets/images/admin/marketing-icon.svg'
import cms from '../../assets/images/admin/cms-icon.svg'
import settings from '../../assets/images/admin/settings-icon.svg'
import arrow from '../../assets/images/admin/down-arrow-icon.svg'

const SidebarContent = props => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const [isOpen1, setIsOpen1] = useState(false);
  const toggle1 = () => setIsOpen1(!isOpen1);
  const [isOpen2, setIsOpen2] = useState(false);
  const toggle2 = () => setIsOpen2(!isOpen2);
  const [isOpen3, setIsOpen3] = useState(false);
  const toggle3 = () => setIsOpen3(!isOpen3);
  const [isOpen4, setIsOpen4] = useState(false);
  const toggle4 = () => setIsOpen4(!isOpen4);
  const [isOpen5, setIsOpen5] = useState(false);
  const toggle5 = () => setIsOpen5(!isOpen5);
  const [isOpen6, setIsOpen6] = useState(false);
  const toggle6 = () => setIsOpen6(!isOpen6);
  const [isOpen7, setIsOpen7] = useState(false);
  const toggle7 = () => setIsOpen7(!isOpen7);
  const history = props.location.pathname
  const subOpen = history == `${process.env.PUBLIC_URL}/manage-roles-permissions/add-roles` ;
  const subOpen1 = history == `${process.env.PUBLIC_URL}/manage-users/add-user`
  const subOpen2 = history == `${process.env.PUBLIC_URL}/manage-b2b-users/add-user` || history == `${process.env.PUBLIC_URL}/manage-b2b-users/user-requests` || history == `${process.env.PUBLIC_URL}/manage-b2b-users/package-deposits`
  const subOpen3 = history == `${process.env.PUBLIC_URL}/manage-artists/add-artist` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-projects` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-pricing` || history == `${process.env.PUBLIC_URL}/manage-artists/project-review` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-payout`
  const subOpen4 = history == `${process.env.PUBLIC_URL}/manage-products/add-products` || history == `${process.env.PUBLIC_URL}/manage-products/featured-products` || history == `${process.env.PUBLIC_URL}/manage-products/category` || history == `${process.env.PUBLIC_URL}/manage-products/reviews-ratings` || history == `${process.env.PUBLIC_URL}/manage-products/configuration`
  const subOpen5 = history == `${process.env.PUBLIC_URL}/manage-orders/b2buser-orders` || history == `${process.env.PUBLIC_URL}/manage-orders/invoice` || history == `${process.env.PUBLIC_URL}/manage-orders/manage-requests` || history == `${process.env.PUBLIC_URL}/manage-promotions` || history == `${process.env.PUBLIC_URL}/manage-sale`
  const subOpen6 = history == `${process.env.PUBLIC_URL}/marketing/add-banner` || history == `${process.env.PUBLIC_URL}/marketing/promotion-banner` || history == `${process.env.PUBLIC_URL}/marketing/collection-management`
  const subOpen7 = history == `${process.env.PUBLIC_URL}/manage-job/applied-job`
  useEffect(()=> {
    if(subOpen){
      setIsOpen(true)
    }
    if(subOpen1){
      setIsOpen1(true)
    }
    if(subOpen2){
      setIsOpen2(true)
    }
    if(subOpen3){
      setIsOpen3(true)
    }
    if(subOpen4){
      setIsOpen4(true)
    }
    if(subOpen5){
      setIsOpen5(true)
    }
    if(subOpen6){
      setIsOpen6(true)
    }
    if(subOpen7){
      setIsOpen7(true)
    }
  },[])
  return (
    <div id="sidebar-menu">
      <ul className="list-unstyled">
        <li>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/dashboard`}>
              <img src={dashboard} alt="icon" />
              <span>Dashboard</span>
            </Link>
          </div>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-roles-permissions` || history == `${process.env.PUBLIC_URL}/manage-roles-permissions/add-roles`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-roles-permissions`} >
              <img src={roles} alt="icon" />
              <span>Manage Roles &amp; Permissions</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-roles-permissions/add-roles`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-roles-permissions/add-roles`})}>Add Roles</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-users` || history == `${process.env.PUBLIC_URL}/manage-users/add-user`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-users`} >
              <img src={users} alt="icon" />
              <span>Manage Users</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle1} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen1}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-users/add-user`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-users/add-user`})}>Add User</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-b2b-users` || history == `${process.env.PUBLIC_URL}/manage-b2b-users/add-user` || history == `${process.env.PUBLIC_URL}/manage-b2b-users/user-requests` || history == `${process.env.PUBLIC_URL}/manage-b2b-users/package-deposits`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-b2b-users`} >
              <img src={b2b_users} alt="icon" />
              <span>Manage B2B Users</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle2} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen2}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-b2b-users/add-user`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-b2b-users/add-user`})}>Add User</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-b2b-users/user-requests`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-b2b-users/user-requests`})}>User Requests</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-b2b-users/package-deposits`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-b2b-users/package-deposits`})}>Manage Packages</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-artists` || history == `${process.env.PUBLIC_URL}/manage-artists/add-artist` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-projects` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-pricing` || history == `${process.env.PUBLIC_URL}/manage-artists/project-review` || history == `${process.env.PUBLIC_URL}/manage-artists/artist-payout`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-artists`} >
              <img src={artists} alt="icon" />
              <span>Manage Artists</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle3} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen3}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-artists/add-artist`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-artists/add-artist`})}>Add Artist</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-artists/artist-projects`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-artists/artist-projects`})}>Manage Projects</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-artists/artist-pricing`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-artists/artist-pricing`})}>Manage Pricing</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-artists/project-review`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-artists/project-review`})}>Manage Review</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-artists/artist-payout`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-artists/artist-payout`})}>Manage Payout</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-products` || history == `${process.env.PUBLIC_URL}/manage-products/add-products` || history == `${process.env.PUBLIC_URL}/manage-products/featured-products` || history == `${process.env.PUBLIC_URL}/manage-products/category` || history == `${process.env.PUBLIC_URL}/manage-products/reviews-ratings` || history == `${process.env.PUBLIC_URL}/manage-products/configuration`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-products`} >
              <img src={products} alt="icon" />
              <span>Manage Products</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle4} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen4}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-products/add-products`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-products/add-products`})}>Add Product</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-products/featured-products`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-products/featured-products`})}>Featured Products</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-products/category`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-products/category`})}>Manage Category</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-products/reviews-ratings`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-products/reviews-ratings`})}>Manage Reviews &amp; Ratings</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-products/configuration`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-products/configuration`})}>Manage Configurations</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-orders/b2cuser-orders` || history == `${process.env.PUBLIC_URL}/manage-orders/b2buser-orders` || history == `${process.env.PUBLIC_URL}/manage-orders/invoice` || history == `${process.env.PUBLIC_URL}/manage-orders/manage-requests`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-orders/b2cuser-orders`} >
              <img src={orders} alt="icon" />
              <span>Manage Orders</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle5} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen5}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-orders/b2buser-orders`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-orders/b2buser-orders`})}>B2B Orders</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-orders/invoice`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-orders/invoice`})}>Manage Invoice</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-orders/manage-requests`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-orders/manage-requests`})}>Manage Requests</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-promotions`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-promotions`})}>Manage Promotions</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-sale`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-sale`})}>Manage Sales</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/marketing` || history == `${process.env.PUBLIC_URL}/marketing/add-banner` || history == `${process.env.PUBLIC_URL}/marketing/promotion-banner` || history == `${process.env.PUBLIC_URL}/marketing/collection-management`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/marketing`} >
              <img src={marketing} alt="icon" />
              <span>Marketing</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle6} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen6}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/marketing/add-banner`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/marketing/add-banner`})}>Add Banner</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/marketing/promotion-banner`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/marketing/promotion-banner`})}>Add Promotion Banner</Link>
              </li>
              <li>
                <Link to={`${process.env.PUBLIC_URL}/marketing/collection-management`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/marketing/collection-management`})}>Manage Collections</Link>
              </li>
            </ul>
          </Collapse>
        </li>

        <li>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/cms`}>
              <img src={cms} alt="icon" />
              <span>CMS</span>
            </Link>
          </div>
        </li>

        <li>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-blog`}>
              <img src={cms} alt="icon" />
              <span>Blog Management</span>
            </Link>
          </div>
        </li>

        <li className={classnames({ active: history === `${process.env.PUBLIC_URL}/manage-job` || history == `${process.env.PUBLIC_URL}/manage-job/applied-job`})}>
          <div className="menu-item">
            <Link to={`${process.env.PUBLIC_URL}/manage-job`} >
              <img src={cms} alt="icon" />
              <span>Job Management</span>
            </Link>
            <a href="javascript:void(0)" onClick={toggle7} className="open-sub"><img src={arrow} alt="down-arrow"/></a>
          </div>
          <Collapse isOpen={isOpen7}>
            <ul className="sub-menu" aria-expanded="false">
              <li>
                <Link to={`${process.env.PUBLIC_URL}/manage-job/applied-job`} className={classnames({ active: history == `${process.env.PUBLIC_URL}/manage-job/applied-job`})}>Applied Jobs</Link>
              </li>
            </ul>
          </Collapse>
        </li>
      </ul>
    </div>
  );
};

class Sidebar extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <React.Fragment>
        <SimpleBar style={{ maxHeight: "100%" }}>
          <SidebarContent {...this.props}/>
        </SimpleBar>
      </React.Fragment>
    );
  }
}

export default withRouter(Sidebar);
