import React, { Component } from 'react';
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { withRouter, Link } from 'react-router-dom';

// profile
import profile from '../../../assets/images/admin/person.jpg';

class ProfileMenu extends Component {

    constructor(props) {
        super(props);
        this.state = {
            menu: false,
        };
        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState(prevState => ({
            menu: !prevState.menu
        }));
    }


    render() {

        return (
            <React.Fragment>
                <Dropdown isOpen={this.state.menu} toggle={this.toggle} className="d-inline-block order-2 order-xl-1" >
                    <DropdownToggle className="btn header-item waves-effect" id="page-header-user-dropdown" tag="button">
                        <div className="d-flex align-items-center">
                            <img className="rounded-circle header-profile-user" src={profile} alt="Header Avatar" />
                            <h5>Mitchell <i className="fas fa-chevron-right"></i><span>ID : 1234ABC5</span></h5>
                        </div>
                    </DropdownToggle>
                    <DropdownMenu right>
                        <DropdownItem tag="a" href={`${process.env.PUBLIC_URL}/my-account`}><i className="mdi mdi-account-circle font-size-17 align-middle mr-1"></i>Account</DropdownItem>
                        <div className="dropdown-divider"></div>
                        <Link
                            to='/logout'
                            className="dropdown-item">
                            <i className="mdi mdi-logout font-size-17 align-middle mr-1"></i>
                            <span>Logout</span>
                        </Link>
                    </DropdownMenu>
                </Dropdown>
            </React.Fragment>
        );
    }
}

export default withRouter(ProfileMenu);


