import React, {useEffect} from 'react'
import { Col, Row, Form, Input, FormGroup, Button } from 'reactstrap';
import Select from 'react-select'

function AddSubCategoryContent(props) {
    const super_category = [
        { value: 'Category 1', label: 'Category 1' },
        { value: 'Category 2', label: 'Category 2' },
    ]
    const category = [
        { value: 'Category 1', label: 'Category 1' },
        { value: 'Category 2', label: 'Category 2' },
    ]
    useEffect(() => {
        document.body.classList.add('bg-secondary');
        return () => {
          // componentWillUnmount events
          document.body.classList.remove('bg-secondary');
        }
      }, [])
    return (
        <>
            <div className="main-title">
                <h1>Add Sub Category</h1>
            </div>
            <Form>
                <Row>
                    <Col md="6">
                        <FormGroup>
                            <label>Super Category</label>
                            <div className="normal-dropdown">
                                <Select
                                    options={super_category}
                                    menuPlacement = "auto"
                                    placeholder={<span></span>}
                                />
                            </div>
                        </FormGroup>
                    </Col>
                    <Col md="6"></Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Category</label>
                            <div className="normal-dropdown">
                                <Select
                                    options={category}
                                    menuPlacement = "auto"
                                    placeholder={<span></span>}
                                />
                            </div>
                        </FormGroup>
                    </Col>
                    <Col md="6"></Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Sub Category</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6"></Col>
                    <Col md="6">
                        <div className="text-center">
                            <Button type="submit" className="common-btn color-blue">ADD SUB CATEGORY</Button>
                        </div>
                    </Col>
                </Row>
            </Form>
        </>
    )
}

export default AddSubCategoryContent
