import React, {useEffect} from 'react'
import { Col, Row, Form, Input, FormGroup, Button } from 'reactstrap';
import Select from 'react-select'


function AddPricingContent(props) {
    const category_values = [
        { value: 'Category 1', label: 'Category 1' },
        { value: 'Category 2', label: 'Category 2' },
    ]
    useEffect(() => {
        document.body.classList.add('bg-secondary');
        return () => {
          // componentWillUnmount events
          document.body.classList.remove('bg-secondary');
        }
      }, [])
    return (
        <>
            <div className="main-title">
                <h1>Add Pricing</h1>
            </div>
            <Form>
                <Row>
                    <Col md="6">
                        <FormGroup>
                            <label>CATEGORY</label>
                            <div className="normal-dropdown">
                                <Select
                                    options={category_values}
                                    menuPlacement = "auto"
                                    placeholder={<span></span>}
                                    defaultValue = {{ value: 'Category 1', label: 'Category 1' }}
                                />
                            </div>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>RETAIL PRICE</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>MARGIN</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>MARKUP</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                </Row>
                <div className="text-center">
                    <Button type="submit" className="common-btn color-blue">SUBMIT</Button>
                </div>
            </Form>
        </>
    )
}

export default AddPricingContent
