import React, { Component } from 'react';
import ReactApexChart from 'react-apexcharts';

class barchart extends Component {
    constructor(props) {
        super(props);

        this.state = {
            options: {
                chart: {
                    toolbar: {
                        show: false,
                    },
                },
                colors: ['#0566FF', '#FF5F48'],
                plotOptions: {
                    bar: {
                        columnWidth: '45%',
                        dataLabels: {
                            show: false
                        },
                    },
                },
                legend: {
                    show: false
                },
                dataLabels: {
                    enabled: false,
                },
                grid: {
                    borderColor: '#dadada',
                    strokeDashArray: 1,
                    xaxis: {
                        lines: {
                            show: true
                        }
                    },   
                    yaxis: {
                        lines: {
                            show: true
                        }
                    }, 
                    row: {
                        opacity: 1
                    },
                    column: {
                        opacity: 1
                    }, 
                },
                stroke: {
                    show: true,
                    width: 1.5,
                    colors: ['#fff']
                },
                xaxis: {
                    categories: ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sept','Oct','Nov','Dec'],
                    axisBorder: {
                        show: false
                    },
                    axisTicks: {
                        show: false
                    }
                },
                yaxis: {
                    tickAmount: 7,
                    forceNiceScale: true,
                    decimalsInFloat: 0,
                },
                stroke: {
                    colors: ['#dadada'],
                },
                fill: {
                    opacity: 1,
                }     
                
            },
            responsive: [
                {
                    breakpoint: 768,
                    options: {
                        plotOptions: {
                            bar: {
                                vertical: false,
                                horizontal: true
                            }
                        },
                        legend: {
                            position: "bottom"
                        }
                    }
                }
            ],
            series: [{
                name: 'Series A',
                data: [7,5,7,7,5.5,7,5,7,5,4,6,7]
            }, {
                name: 'Series B',
                data: [4,3,4,5,6,7,4,3,4.5,5.5,6,5.5]
            }],
        }
    }
    render() {
        return (
            <React.Fragment>
                <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height="500" />
            </React.Fragment>
        );
    }
}

export default barchart;