import React, {useState,useEffect} from 'react'
import { Col, Row, Form, Input, FormGroup, Button } from 'reactstrap';
import DatePicker from 'react-date-picker'
import PhoneInput from 'react-phone-input-2'
import Select from 'react-select'

function AddUserContent(props) {
    const package_values = [
        { value: 'Package 1', label: 'Package 1' },
        { value: 'Package 2', label: 'Package 2' },
    ]
    useEffect(() => {
        document.body.classList.add('bg-secondary');
        return () => {
          // componentWillUnmount events
          document.body.classList.remove('bg-secondary');
        }
      }, [])
    const [date, onChange] = useState(new Date())
    return (
        <>
            <div className="main-title">
                <h1>Add B2B User</h1>
            </div>
            <Form>
                <Row>
                    <Col md="6">
                        <FormGroup>
                            <label>FIRST NAME</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>LAST NAME</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>EMAIL ADDRESS</label>
                            <Input type="email"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Phone Number</label>
                            <div className="country-code-list">
                                <PhoneInput
                                    country={'in'}
                                    // value={this.state.phone}
                                    // onChange={phone => this.setState({ phone })}
                                />
                            </div>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup className="date-field">
                            <label>Date of Birth</label>
                            <DatePicker
                                onChange={onChange}
                                value={date}
                                className="form-control"
                            />
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup className="date-field">
                            <label>Deposit Package</label>
                            <div className="normal-dropdown">
                                <Select
                                    options={package_values}
                                    menuPlacement = "auto"
                                    placeholder={<span></span>}
                                />
                            </div>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Password</label>
                            <Input type="password"></Input>
                        </FormGroup>
                    </Col>
                    <Col md="6">
                        <FormGroup>
                            <label>Note</label>
                            <Input type="text"></Input>
                        </FormGroup>
                    </Col>
                </Row>
                <div className="text-center">
                    <Button type="submit" className="common-btn color-blue">SUBMIT</Button>
                </div>
            </Form>
        </>
    )
}

export default AddUserContent
